import { Component } from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-cash-by-code-history02',
  templateUrl: 'cash-by-code-history02.html',
})
export class CashByCodeHistory02Page {
  iCashRemitDetails;

  constructor(public navCtrl: NavController,
              public alertCtrl: AlertController,
              public navParams: NavParams) {
    this.iCashRemitDetails = JSON.parse(navParams.get("iCashRemitDetails"));
  }


  showICashRemitDetails = function (listItem) {
    let status = "";
    if (listItem.status == 1) {
      status = "Success";
    }
    else if (listItem.status == 2) {
      status = "Requested";
    }
    else if (listItem.status == 3) {
      status = "Canceled";
    }

    else if (listItem.status == 9) {
      status = "Failed";
    }
    else {
      status = "n/a"
    }
    let alertData = {
      "Source Account ": listItem.srcAccount,
      "Beneficiary Mobile No ": listItem.destAccount,
      "Beneficiary Name ": listItem.destAccountTitle,
      "Transaction Date ": new Date(listItem.transactionDateTime).toLocaleString(),//$filter('date')(listItem.rechargeDate,'dd/MM/yyyy hh:mm:ss a'),
      "Account ": listItem.transferAmount,
      "Transfer Charge ": listItem.transferCharge,
      "Vat On Charge ": listItem.vatOnTransferCharge,
      "Status ": status
    };

    let radioAlert = this.alertCtrl.create();
    radioAlert.setTitle('iCashRemit Details');
    radioAlert.setCssClass("alertLogCss");
    radioAlert.setMessage(this.generateMessage(alertData));
    radioAlert.addButton('Cancel');
    radioAlert.addButton({
      text: 'OK',
      handler: data => {

      }
    });
    radioAlert.present();
    setTimeout(() => {
      radioAlert.dismiss();
    }, 30000);
  };
  generateMessage = function (itemValue) {

    let message = "";
    Object.keys(itemValue).forEach(key => {
      let value = itemValue[key];
      message = message +
        '<ion-item style="margin-top: 16px"><b>' + key.replace("_", " ") + " : " + "</b>" + value + '<br></ion-item>';
    });
    return message;
  };

  ionViewDidLoad() {

  }

}



